import React from "react"
import type { Icon, IconWeight } from "@phosphor-icons/react"

interface IconWrapperProps {
  icon: Icon
  weight?: IconWeight
  size?: number | string
  className?: string
  color?: string
  "aria-hidden"?: boolean | "true" | "false"
  "aria-label"?: string
}

/**
 * IconWrapper - 统一图标样式包装组件
 *
 * 默认使用 duotone 风格，符合 Digital Garden 的手绘温暖风格
 * 支持通过 weight 属性切换图标风格：thin, light, regular, bold, fill, duotone
 */
export function IconWrapper({
  icon: IconComponent,
  weight = "duotone",
  size = 20,
  className = "",
  color,
  "aria-hidden": ariaHidden = true,
  "aria-label": ariaLabel,
}: IconWrapperProps): React.ReactElement {
  return (
    <IconComponent
      weight={weight}
      size={size}
      className={className}
      color={color}
      aria-hidden={ariaHidden}
      aria-label={ariaLabel}
    />
  )
}

export default IconWrapper
