import React, { useState, useMemo } from "react"
import { Link, graphql, PageProps } from "gatsby"
import { IconWrapper } from "../components/icons"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {
  Hash,
  MagnifyingGlass,
  X,
  ChartBar,
  BookOpen,
} from "@phosphor-icons/react"

interface TagNode {
  fieldValue: string
  totalCount: number
}

interface TagsPageData {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    group: TagNode[]
  }
}

// Kebab case converter for tag URLs
const kebabCase = (str: string): string =>
  str
    .replace(/([a-z])([A-Z])/g, "$1-$2")
    .replace(/[\s_]+/g, "-")
    .toLowerCase()

// Generate organic border radius based on tag name for consistent but varied shapes
const getOrganicRadius = (str: string): string => {
  const hash = str.split("").reduce((acc, char) => acc + char.charCodeAt(0), 0)
  const variations = [
    "16px 20px 14px 22px",
    "20px 16px 22px 14px",
    "14px 22px 16px 20px",
    "22px 14px 20px 16px",
    "18px 24px 12px 20px",
    "12px 20px 18px 24px",
  ]
  return variations[hash % variations.length]
}

// Get tag size class and color intensity based on article count
const getTagStyle = (
  count: number,
  maxCount: number
): { sizeClass: string; colorIntensity: number } => {
  const ratio = count / maxCount

  let sizeClass = ""
  if (ratio >= 0.8) {
    sizeClass = "text-base sm:text-lg px-3 sm:px-5 py-2 sm:py-2.5"
  } else if (ratio >= 0.6) {
    sizeClass = "text-sm sm:text-base px-2.5 sm:px-4 py-1.5 sm:py-2"
  } else if (ratio >= 0.4) {
    sizeClass = "text-sm px-2.5 py-1.5 sm:py-2"
  } else if (ratio >= 0.2) {
    sizeClass = "text-xs px-2.5 py-1.5 sm:py-1.5"
  } else {
    sizeClass = "text-xs px-2 py-1.5"
  }

  return { sizeClass, colorIntensity: ratio }
}

// Get color style based on intensity - Borderless design using background layers
const getColorStyle = (intensity: number): string => {
  // Higher intensity = more prominent color
  if (intensity >= 0.8) {
    return "bg-accent text-accent-foreground shadow-md hover:shadow-lg"
  } else if (intensity >= 0.6) {
    return "bg-accent-subtle text-accent shadow-sm hover:shadow-md"
  } else if (intensity >= 0.4) {
    return "bg-bg-card text-text-primary shadow-sm hover:shadow-md hover:bg-bg-secondary"
  } else if (intensity >= 0.2) {
    return "bg-bg-secondary text-text-secondary hover:bg-bg-tertiary"
  } else {
    return "bg-bg-secondary text-text-muted hover:bg-bg-tertiary"
  }
}

/**
 * Tags Page Component - Digital Garden Style
 * Displays an organic-shaped tag cloud with tags sized and colored by popularity
 */
const TagsPage: React.FC<PageProps<TagsPageData>> = ({
  data,
  location,
}) => {
  const siteTitle = data.site.siteMetadata.title
  const tags = data.allMarkdownRemark.group
  const [searchQuery, setSearchQuery] = useState("")

  // Sort tags by count (descending) for consistent display
  const sortedTags = useMemo(
    () => [...tags].sort((a, b) => b.totalCount - a.totalCount),
    [tags]
  )

  // Filter tags based on search query
  const filteredTags = useMemo(() => {
    if (!searchQuery.trim()) return sortedTags
    const query = searchQuery.toLowerCase()
    return sortedTags.filter((tag) =>
      tag.fieldValue.toLowerCase().includes(query)
    )
  }, [sortedTags, searchQuery])

  // Calculate max count for size scaling
  const maxCount = sortedTags.length > 0 ? sortedTags[0].totalCount : 1

  // Calculate total articles
  const totalArticles = tags.reduce((sum, tag) => sum + tag.totalCount, 0)

  // Get tag for URL
  const getTagSlug = (tagName: string): string => {
    // Handle Chinese tags by using the tag name directly in URL
    return encodeURIComponent(tagName)
  }

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title="标签"
        description="浏览所有文章标签，发现感兴趣的内容"
        keywords={["标签", "tags", "blog", "文章分类"]}
      />

      {/* Page Header */}
      <header className="mb-8 text-center fade-in">
        <div className="inline-flex items-center justify-center w-12 h-12 rounded-xl bg-accent-subtle mb-3">
          <IconWrapper icon={Hash} size={24} className="text-accent" />
        </div>
        <h1
          className="text-2xl font-bold text-text-primary mb-2"
          style={{ fontFamily: '"Noto Serif SC", Georgia, serif' }}
        >
          标签花园
        </h1>
        <p className="text-text-secondary text-sm max-w-md mx-auto">
          共 <span className="text-accent font-semibold">{tags.length}</span> 个标签，
          <span className="text-accent font-semibold"> {totalArticles}</span> 篇文章
        </p>
      </header>

      {/* Search Input */}
      {tags.length > 0 && (
        <div className="max-w-md mx-auto mb-8 fade-in" style={{ animationDelay: "0.1s" }}>
          <div className="relative">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <IconWrapper
                icon={MagnifyingGlass}
                size={18}
                className="text-text-muted"
              />
            </div>
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="搜索标签..."
              className="w-full pl-10 pr-9 py-2.5 min-h-[40px] rounded-lg bg-bg-secondary text-text-primary placeholder-text-muted focus:outline-none focus:bg-bg-card focus:ring-2 focus:ring-accent/20 transition-all duration-200 text-sm border-0"
            />
            {searchQuery && (
              <button
                onClick={() => setSearchQuery("")}
                className="absolute inset-y-0 right-0 pr-2 flex items-center"
                aria-label="清除搜索"
              >
                <div className="p-1 rounded-full bg-bg-secondary hover:bg-bg-tertiary transition-colors">
                  <IconWrapper icon={X} size={12} weight="bold" className="text-text-muted" />
                </div>
              </button>
            )}
          </div>
          {searchQuery && (
            <p className="mt-2 text-xs text-text-muted text-center">
              找到 {filteredTags.length} 个匹配的标签
            </p>
          )}
        </div>
      )}

      {/* Tag Cloud */}
      {tags.length === 0 ? (
        <div className="text-center py-16 fade-in">
          <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-bg-secondary mb-3">
            <IconWrapper icon={Hash} size={32} className="text-text-muted" />
          </div>
          <p className="text-text-secondary text-sm">暂无标签</p>
        </div>
      ) : filteredTags.length === 0 ? (
        // Empty search state
        <div className="text-center py-12 fade-in">
          <div className="inline-flex items-center justify-center w-14 h-14 rounded-full bg-bg-secondary mb-3">
            <IconWrapper icon={MagnifyingGlass} size={28} className="text-text-muted" />
          </div>
          <h3 className="text-base font-semibold text-text-primary mb-2">
            未找到匹配的标签
          </h3>
          <p className="text-text-secondary text-sm mb-4">
            没有找到包含「{searchQuery}」的标签
          </p>
          <button
            onClick={() => setSearchQuery("")}
            className="inline-flex items-center gap-1.5 px-3 py-1.5 rounded-lg bg-accent-subtle text-accent text-sm hover:bg-accent/20 transition-colors"
          >
            <IconWrapper icon={X} size={14} />
            清除搜索
          </button>
        </div>
      ) : (
        <div className="fade-in-up" style={{ animationDelay: "0.2s" }}>
          {/* Tag Cloud Container */}
          <div className="flex flex-wrap justify-center gap-2 sm:gap-3 md:gap-4">
            {filteredTags.map((tag, index) => {
              const { sizeClass, colorIntensity } = getTagStyle(
                tag.totalCount,
                maxCount
              )
              const colorClass = getColorStyle(colorIntensity)
              const organicRadius = getOrganicRadius(tag.fieldValue)

              return (
                <Link
                  key={tag.fieldValue}
                  to={`/tags/${kebabCase(tag.fieldValue)}/`}
                  className={`
                    group relative inline-flex items-center gap-1.5
                    font-medium
                    border-0
                    transition-all duration-300 ease-out
                    hover:-translate-y-1
                    focus:outline-none focus:ring-2 focus:ring-accent focus:ring-offset-2
                    focus:ring-offset-bg-primary
                    ${sizeClass} ${colorClass}
                  `}
                  style={{
                    borderRadius: organicRadius,
                    animationDelay: `${index * 30}ms`,
                  }}
                >
                  {/* Tag Icon */}
                  <IconWrapper
                    icon={Hash}
                    size={tag.totalCount >= maxCount * 0.6 ? 20 : 16}
                    className="opacity-60 group-hover:opacity-100 transition-opacity duration-200"
                    weight="duotone"
                  />

                  {/* Tag Name */}
                  <span>{tag.fieldValue}</span>

                  {/* Article Count Badge */}
                  <span
                    className={`
                      inline-flex items-center justify-center
                      rounded-full
                      min-w-[1.5em] px-1.5
                      text-xs font-semibold
                      transition-colors duration-200
                      ${
                        colorIntensity >= 0.6
                          ? "bg-white/20 text-white"
                          : "bg-bg-secondary text-text-muted group-hover:bg-accent-subtle group-hover:text-accent"
                      }
                    `}
                    style={{ borderRadius: "9999px" }}
                  >
                    {tag.totalCount}
                  </span>
                </Link>
              )
            })}
          </div>

          {/* Stats Section */}
          <div className="mt-12 grid grid-cols-2 sm:grid-cols-3 gap-3 max-w-2xl mx-auto">
            {/* Most Popular Tag */}
            {sortedTags.length > 0 && (
              <div className="col-span-2 sm:col-span-1 text-center p-4 rounded-xl bg-bg-card shadow-sm hover:shadow-md transition-shadow duration-200">
                <IconWrapper
                  icon={ChartBar}
                  size={20}
                  className="mx-auto mb-1.5 text-accent"
                />
                <div
                  className="text-base font-bold text-text-primary mb-1 truncate px-2"
                  title={sortedTags[0].fieldValue}
                >
                  {sortedTags[0].fieldValue}
                </div>
                <div className="text-xs text-text-muted">
                  最热标签 ({sortedTags[0].totalCount} 篇)
                </div>
              </div>
            )}

            {/* Average Articles per Tag */}
            <div className="text-center p-4 rounded-xl bg-bg-card shadow-sm hover:shadow-md transition-shadow duration-200">
              <IconWrapper
                icon={BookOpen}
                size={20}
                className="mx-auto mb-1.5 text-accent"
              />
              <div className="text-xl font-bold text-text-primary mb-1">
                {Math.round(totalArticles / tags.length)}
              </div>
              <div className="text-xs text-text-muted">平均篇数</div>
            </div>

            {/* Total Tags */}
            <div className="text-center p-4 rounded-xl bg-bg-card shadow-sm hover:shadow-md transition-shadow duration-200">
              <IconWrapper
                icon={Hash}
                size={20}
                className="mx-auto mb-1.5 text-accent"
              />
              <div className="text-xl font-bold text-text-primary mb-1">
                {tags.length}
              </div>
              <div className="text-xs text-text-muted">标签总数</div>
            </div>
          </div>

          {/* Alphabetical List */}
          <div className="mt-12 pt-6">
            <h2
              className="text-base font-semibold text-text-primary mb-4 text-center"
              style={{ fontFamily: '"Noto Serif SC", Georgia, serif' }}
            >
              按字母排序
            </h2>
            <div className="flex flex-wrap justify-center gap-1.5">
              {[...tags]
                .sort((a, b) =>
                  a.fieldValue.localeCompare(b.fieldValue, "zh-CN")
                )
                .map((tag) => (
                  <Link
                    key={`alpha-${tag.fieldValue}`}
                    to={`/tags/${kebabCase(tag.fieldValue)}/`}
                    className="
                      inline-flex items-center gap-1
                      px-2.5 py-1.5 text-xs
                      text-text-muted
                      hover:text-accent hover:bg-accent-subtle
                      rounded-md
                      transition-colors duration-200
                      min-h-[36px]
                    "
                  >
                    <span>{tag.fieldValue}</span>
                    <span className="text-text-subtle">({tag.totalCount})</span>
                  </Link>
                ))}
            </div>
          </div>
        </div>
      )}
    </Layout>
  )
}

export default TagsPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(limit: 2000, filter: {frontmatter: {published: {ne: false}}}) {
      group(field: {frontmatter: {tags: SELECT}}) {
        fieldValue
        totalCount
      }
    }
  }
`
